.skillsBlock {
    padding: 100px 0;
    min-height: 100vh;
    background-color: #1b1b1b;
}

.skillsContainer {
    flex-wrap: nowrap;
    flex-direction: column;
    justify-content: flex-start;

}

.skills {
    margin-top: 30px;
    border: 1px solid;
    width: 100%;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    
}

.title {
    background-color:yellow
    

}
@media screen and (max-width: 710px) {
    .skills {
        flex-direction: column;
        align-items: center;
    }
    .skills + .skills {
        margin-top: 0px;
    }
}
